.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.tower {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 200px;
  width: 120px;
}

.tower-label {
  font-weight: bold;
  margin-bottom: 10px;
}

.peg {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  justify-content: flex-end;
  height: 200px;
  width: 10px;
  background-color: #ccc;
  position: relative;
  margin-top: 10px;
}

.disk {
  height: 20px;
  border-radius: 4px;
  margin: 4px 0;
}

.disk-1 {
  width: 40px;
  background-color: #4CAF50;
}

.disk-2 {
  width: 60px;
  background-color: #2196F3;
}

.disk-3 {
  width: 80px;
  background-color: #FF9800;
}

.disk-4 {
  width: 100px;
  background-color: purple;
}

